<template>
    <div class="big">
        <div class="field">
            <h2>Enter your name:</h2>
            <input type="text" v-model="name">
            <br> <br>
            <button @click="login()">Enter</button>
        </div>
    </div>
</template>

<script>
import $cookies from 'vue-cookies';
export default {
    data() {
        return {
            name: '',
        }
    },
    methods: {
        login() {
            this.$emit('setAuthor', this.name);
            $cookies.set('author', this.name);
        }
    },
    props: [
        'author',
    ]
}
</script>

<style scoped>
.big {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.field {
    padding: 20px;
    background-color: white;
    border-radius: 20px;
}
.field > input {
    width: 93%;
    height: 20px;
    border: 1px solid grey;
}
.field > button {
    border: none;
    background-color: rgb(29, 197, 85);
    color: white;
    padding: 5px;
    border-radius: 13px;
    width: 100%;
}
</style>